@import "components-mixins/_size-control.scss";
@import "mixins/clearfix";

//@VAR nav-group
$nav-group-class				: '.#{$default-prefix}nav-group' !default;
$nav-group-item-class			: '.nav-group-item' !default;

$nav-group-bd-radius			: $default-bd-radius !default;
$nav-group-bd-color				: $default-bd-color !default;
$nav-group-bg-color				: $default-bg-color !default;
$nav-group-item-active-bg-color			: $default-active-bg-color !default;
$nav-group-item-hover-bg-color			: $default-hover-bg-color !default;
$nav-group-item-disabled-bg-color		: $default-disabled-bg-color !default;
$nav-group-active-cursor		: $default-active-cursor !default;
$nav-group-padding				: 5px !default;
$nav-group-item-padding			: 2em !default;
$nav-group-title-color					: #666 !default;

//!VAR nav-group

// @mixin make-nav-group() {
	// 列表项
